@media print {
    body, html, #wrapper {
        transform: scale(1.55);
    }
    .print_content{
        top:150px;
    }
}


.print_content{
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    background:transparent; 
    position: relative;
    width: 62vw;
    height: 70vw;
}

a{
    background: transparent;  
    color: rgb(213, 224, 230);
}

.left_space{
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    max-width: 17.7vw;
    min-width: 17.7vw;
    position: relative;
    margin: 0vw;
    top: 0vw;
    left: 0vw;
    width: 17.7vw;
    height: 68vw;
    padding: 1vw;
    background: rgb(55, 103, 129); 
    color: rgb(213, 224, 230);
    border-radius: 0.2vw 0vw 0vw 0.2vw;
    
}

.main_space{
    max-width: 62vw;
    min-width: 62vw;
    position: relative;
    margin: 2vw;
    margin-right: auto;
    margin-left: auto;
    width: 62vw;
    height: 70vw;
    background: rgb(213, 224, 230);
    color: rgb(222, 235, 235);
    border-radius: 0.2vw;
    box-shadow: 0 0 1vw rgba(0,0,0,0.3);
}

.right_space{
    position: absolute;
    width: 40vw;
    height: 68vw;
    background: transparent;
    top: 1vw;
    left: 21vw;
}

.image_main{
    text-align: center;
    max-width: 15vw;
    min-width: 15vw;
    position: relative;
    top: 1.5vw;
    border-radius: 0.2vw;
    box-shadow: 0 0 1vw rgba(0,0,0,0.5);
}

.name_space{
    text-align: center;
    margin-top: 0.5vw;
    margin-bottom: 1vw;
    top: 1.5vw;
    position: relative;
    background: transparent;
    font-size: 1.6vw;
    font-family: DejaVu Sans Mono, monospace;
    color: rgb(214, 225, 231);
}