.btn_space{
    position: relative;
    top: 5.5vw;
    margin-left: auto;
    margin-right: auto;
    background: transparent;
}

.btn{
    text-align: center;
    padding: 0.5vw 7vw;
    color: rgb(234, 238, 241);
    font-size: 1.8vw;
    background: rgb(32, 61, 77);
    border: none;
    cursor: pointer;
    border-radius: 0.2vw;
}

.btn:hover{
    cursor: pointer;
    background: rgb(34, 52, 65);
}

.btn:active{
    background: rgb(23, 37, 46);
    cursor: pointer;
}