.image_wth_txt_div{
    margin-right: auto;
    margin-left: auto;
    margin: 1vw;
    background: transparent;
    font-size: 2vw;
    padding: 1vw;
    color: rgb(223, 235, 235);
    word-wrap: break-word;
}

.image_wth_txt_txt{
    position: relative;
    margin-right: auto;
    margin-left: auto;
    background: transparent;
}

.image_wth_txt{
    position: relative;
    max-width: 50vw;
    min-width: 50vw;
    margin: 1.2vw;
    margin-right: auto;
    margin-left: auto;
    background: rgb(35, 37, 37);
    border-radius: 0.2vw;
    box-shadow: 0 0 2vw rgba(68, 48, 48, 0.8);
}

.header_space{
    margin: 2vw;
    margin-right: auto;
    margin-left: auto;
    width: 62vw;
    padding: 0.1vw;
    background: rgb(55, 103, 129);
    color: rgb(222, 235, 235);
    border-radius: 0.2vw;
    box-shadow: 0 0 1.5vw rgba(0,0,0,0.3);
    font-size: 3vw;
}
