
.home_page{
    position:relative;
    top: -2vw;
    margin-left:auto;
    margin-right:auto;
    font-family:Andale Mono, monospace;
    height: 31vw;
    width:62vw;
    background: rgb(55, 103, 129);
    border-radius: 0.2vw;
    box-shadow: 0 0 2vw rgba(0,0,0,0.5);
    font-size: 2vw;
    font-weight: bold;
}

.home_page_img{
    position:absolute;
    top: 4.5vw;
    left: 3vw;
    background-color: transparent;
}

.home_page_txt{
    text-align: center;
    margin: 2vw;
    background-color: transparent;
    font-size:  2.2vw;
    color:rgb(213, 224, 230);
}

.home_page_btn_my_proj{
    margin-top: 6vw;
    margin-left: 21.7vw;
    background: transparent;
}

.home_page_right_space{
    position:relative;
    right:-10.5vw;
    height: 31vw;
    width: 10vw;
    background-color: rgb(213, 224, 230);
    border-left: 2.5vw solid transparent;
    -ms-transform: skew(10deg, 0deg);
    -webkit-transform: skew(10deg, 0deg);
    transform: skew(10deg, 0deg);
}

.home_page_right_space1{
    position:relative;
    right:-2.5vw;
    background-color: rgb(213, 224, 230);
    height: 31vw;
    width: 24.8vw;
    border-radius: 0px 0.2vw 0.2vw 0px;
    -ms-transform: skew(-10deg, 0deg);
    -webkit-transform: skew(-10deg, 0deg);
    transform: skew(-10deg, 0deg);
}

.home_page_txt_position{
    left: -2vw;
    width: 23vw;
    position: relative;
    top: 12vw;
    background-color: transparent;
    font-size: 2.2vw;
    color: rgb(45, 82, 102);
}

