.link_play_game_space{
    position: relative;
    margin-left: auto;
    margin-right: auto;
    background-color:  transparent;
}

.link_play_game{
    text-decoration: none;
    position: relative;
    top: -2vw;
    background-color:  rgb(41, 70, 85);
    margin-left: auto;
    margin-right: auto;
    width: 20vw;
    border-radius: 0.2vw;
    margin: 0.2vw;
    color: #b4c9d1;
    text-align: center;
    padding: 0.8vw 6.5vw; 
}

.link_play_game:hover {
    background-color: #26313580;
  }

  .link_play_game:active {
    background-color: #2a393dd7;
  }

  